import React, { useCallback, useState } from 'react'
import styled from 'styled-components'
import * as bsc from '@binance-chain/bsc-use-wallet'
import useModal from '../../../hooks/useModal'
import WalletProviderModal from '../../WalletProviderModal'
import AccountModal from './AccountModal'
import Button from '../../Button'
import { useHistory } from 'react-router-dom'
import { useMediaQuery } from 'react-responsive'
// import { useWallet, UseWalletProvider } from 'use-wallet'
// import { useWeb3React } from '@web3-react/core'
// import Web3 from 'web3'
// import { Web3Provider } from '@ethersproject/providers'
import WalletConnect from "@walletconnect/client";
import QRCodeModal from "@walletconnect/qrcode-modal";

interface AccountButtonProps {
  setMobileAccount?: (mobileAddress: any) => void
}

// const getLibrary = (provider: any): Web3Provider => {
//   const library = new Web3Provider(provider)
//   library.pollingInterval = 12000
//   return library
// }

const AccountButton: React.FC<AccountButtonProps> = ({setMobileAccount}) => {
  const history = useHistory()
  const { account, connect } = bsc.useWallet()
  const [workAccount, setWorkAccount] = useState('')

  const [onPresentAccountModal] = useModal(<AccountModal />)

  const isMobile = useMediaQuery({
    query: '(max-width: 767px)'
  })

  const handleProvide = async (providerTitle: any) => {
    if(!isMobile) {
      if(providerTitle === 'injected') {
        await (window as any).ethereum
          .request({
              method: 'wallet_switchEthereumChain',
              params: [{ chainId: '0x38' }], //bsc 0x38 goerli 0x5
          })
          .then(() => {
          })
          .catch(async (err: any) => {
              if (err.code === 4902) {
                  // addChain()
              }
          });
      }
    }

    if(providerTitle == 'walletconnect') {
      // wallet.connect('walletconnect')

      //   const walletconnect = new WalletConnectConnector({
      //     rpc: { [56]: 'https://bsc-dataseed1.ninicoin.io/' },
      //     bridge: 'https://pancakeswap.bridge.walletconnect.org/',
      //     qrcode: true,
      //     pollingInterval: 12000,
      //   })

      //   activate(walletconnect, async (error: Error) => {
      //     console.log(error);
      //   })

    
      const bridge = "https://bridge.walletconnect.org";

      // create new connector
      const connector = new WalletConnect({ bridge, qrcodeModal: QRCodeModal });

      // check if already connected
      if (!connector.connected) {
        // create new session
        await connector.createSession();
      } else {
        await connector.killSession();
      }

      connector.on("connect", (error: any, payload: any) => {

        if (error) {
          throw error;
        }

        setMobileAccount(payload.params[0].accounts[0]);
        setWorkAccount(payload.params[0].accounts[0])
      });

      connector.on("disconnect", (error: any, payload: any) => {

        if (error) {
          throw error;
        }

      });

    } else {
      connect(providerTitle)
    }
  }

  const [onPresentWalletProviderModal] = useModal(
    <WalletProviderModal onClick={handleProvide} />,
    'provider',
  )


  const handleUnlockClick = useCallback(() => {
    onPresentWalletProviderModal()
  }, [onPresentWalletProviderModal])

  const getAccountAddress = () => {
    if (account) {
      var address =
        account.toString().substring(0, 4) +
        '...' +
        account.toString().substr(account.length - 4)
      return address
    } else if(workAccount) {
      var addresss =
      workAccount.toString().substring(0, 4) +
        '...' +
        workAccount.toString().substr(workAccount.length - 4)
      return addresss
    }
  }

  if (account) {
    history.push('/dashboard')
  // } else {
  //   history.push('/')
  }

  // const wallet = useWallet()

  return (
    <StyledAccountButton>
      {(!account && !workAccount) ? (
        <Button
          disabled={false}
          onClick={handleUnlockClick}
          size="lg"
          text="Connect wallet"
        />
      ) : (
        <Button
          onClick={onPresentAccountModal}
          size="md"
          text={getAccountAddress()}
        />
      )}
    </StyledAccountButton>
  )
}

const StyledAccountButton = styled.div``

export default AccountButton
